import { Component, OnInit } from '@angular/core';
import {Playlist, PlaylistService} from "../../services/api/playlist.service";
import {PlayerService} from "../../services/player.service";
import {NavController} from "@ionic/angular";

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.page.html',
  styleUrls: ['./dashboard.page.scss'],
})
export class DashboardPage implements OnInit {
    public playlists: Playlist[];

    constructor(private playlistService: PlaylistService,
                public navController: NavController) { }

  ngOnInit() {
    this.playlistService.subscribe(playlists => this.playlists = playlists);
  }
    async addPlaylist() {
        this.playlistService.create().then(playlist => {
            this.navController.navigateForward('/playlist/' + playlist.id);
        });
    }
}
